<script>
/* import Parallax from "parallax-js"; */

/* window.onload = function() {
  var scene = document.getElementById("scene");
  var parallaxInstance = new Parallax(scene);
}; */

export default {
  name: "Main",

  components: {
    /* Parallax */
  },

  data() {
    return {
      url: "",
      id: "",
      shared: "",
      datos: {
        de: "",
        para: "",
        mensaje: ""
      }
    };
  },

  created: function() {},

  methods: {
    onSubmit() {
      this.$validator
        .validateAll()
        .then(result => {
          if (result) {
            document.querySelector(".loading").style.display = "block";

            this.$http
              .post("/api/out/valentin", JSON.stringify(this.datos))
              .then(function(response) {
                this.modal(response.data.data.url, response.data.data.id);
              }),
              function(response) {};
          }
        })
        .catch(e => {
          console.log(e);
        });
    },

    modal(url, id) {
      this.url = "" + url;
      this.id = id;
      this.shared =
        "https://www.trilce.edu.pe/envia-un-mensaje-de-amor-y-amistad/" + id;

      document.querySelector(".loading").style.display = "none";
      this.$modal.show("modal");
    }
  },

  beforeMount() {
    var id = window.location.hash.substring(1);
    if (id) {
      this.$http.get("/api/out/valentin/" + id).then(function(response) {
        this.modal(response.data.data.url, response.data.data.id);
      }),
        function(response) {};
    }
  }
};
</script>
